
"use client";

import React, { useRef, useEffect, useMemo, useState } from 'react';
import * as THREE from 'three';
import { useThemeContext } from '@/contexts/ThemeContext';

const ThreeCanvas: React.FC = () => {
  const mountRef = useRef<HTMLDivElement>(null);
  const animationFrameIdRef = useRef<number | null>(null);
  const { theme } = useThemeContext();

  const rendererRef = useRef<THREE.WebGLRenderer | null>(null);
  const sceneRef = useRef<THREE.Scene | null>(null);
  const cameraRef = useRef<THREE.PerspectiveCamera | null>(null);
  
  const starFieldRef = useRef<THREE.Points | null>(null);
  const spiralRef = useRef<THREE.Points | null>(null);
  const spiral2Ref = useRef<THREE.Points | null>(null);

  const starsMaterialRef = useRef<THREE.PointsMaterial | null>(null);
  const spiralMaterialRef = useRef<THREE.PointsMaterial | null>(null);
  const spiralMaterial2Ref = useRef<THREE.PointsMaterial | null>(null);

  const [currentThemeColors, setCurrentThemeColors] = useState({
    foreground: new THREE.Color(theme === 'dark' ? 0xffffff : 0x333333),
    primary: new THREE.Color(0x00bcd4),
    secondary: new THREE.Color(0xff4081),
    background: new THREE.Color(theme === 'dark' ? 0x0a192f : 0xf0f0f0),
  });

  // Effect to update colors when theme changes
  useEffect(() => {
    if (typeof document === 'undefined') return;

    const computedRootStyle = getComputedStyle(document.documentElement);
    const getHSLColor = (cssVar: string, fallbackHex: string | number) => {
      const hslString = computedRootStyle.getPropertyValue(cssVar).trim();
      if (hslString) {
        const [h, s, l] = hslString.split(' ').map(val => parseFloat(val.replace('%', '')));
        if (!isNaN(h) && !isNaN(s) && !isNaN(l)) {
          const color = new THREE.Color();
          color.setHSL(h / 360, s / 100, l / 100);
          return color;
        }
      }
      return new THREE.Color(fallbackHex);
    };
    
    setCurrentThemeColors({
      foreground: getHSLColor('--foreground', theme === 'dark' ? 0xE0E0E0 : 0x222222),
      primary: getHSLColor('--primary', 0x00bcd4),
      secondary: getHSLColor('--secondary', 0xff4081),
      background: getHSLColor('--background', theme === 'dark' ? 0x0a192f : 0xf0f0f0),
    });

  }, [theme]);


  useEffect(() => {
    const currentMount = mountRef.current;
    if (!currentMount || typeof window === 'undefined') return;

    let canvasWidth = currentMount.clientWidth;
    let canvasHeight = currentMount.clientHeight;

    if (canvasWidth === 0 || canvasHeight === 0) {
      console.warn("ThreeCanvas: Mount point has zero dimensions. Setup deferred.");
      return;
    }

    const scene = new THREE.Scene();
    sceneRef.current = scene;
    const camera = new THREE.PerspectiveCamera(75, canvasWidth / canvasHeight, 0.1, 1000);
    cameraRef.current = camera;
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    rendererRef.current = renderer;
    
    renderer.setSize(canvasWidth, canvasHeight);
    renderer.setPixelRatio(window.devicePixelRatio);
    
    while (currentMount.firstChild) {
      currentMount.removeChild(currentMount.firstChild);
    }
    currentMount.appendChild(renderer.domElement);
    
    camera.position.z = 5;
    
    // Stars
    const starsGeometry = new THREE.BufferGeometry();
    starsMaterialRef.current = new THREE.PointsMaterial({
      size: 0.02,
      transparent: true,
      opacity: 0.7
    });
    
    const starsVertices = [];
    for (let i = 0; i < 20000; i++) {
      const x = (Math.random() - 0.5) * 30;
      const y = (Math.random() - 0.5) * 30;
      const z = (Math.random() - 0.5) * 30;
      starsVertices.push(x, y, z);
    }
    starsGeometry.setAttribute('position', new THREE.Float32BufferAttribute(starsVertices, 3));
    starFieldRef.current = new THREE.Points(starsGeometry, starsMaterialRef.current);
    scene.add(starFieldRef.current);
    
    // Spiral 1
    const spiralGeometry = new THREE.BufferGeometry();
    spiralMaterialRef.current = new THREE.PointsMaterial({
      size: 0.08,
      transparent: true,
      opacity: 0.3 // Adjusted opacity
    });
    const spiralPoints = [];
    const a = 0.17; 
    const b = 0.1; 
    for (let i = 0; i < 20000; i++) {
      const t = i * 0.01;
      const x = a * Math.exp(b * t) * Math.cos(t);
      const y = a * Math.exp(b * t) * Math.sin(t);
      const z = 0; 
      const jitter = Math.random() * 0.015;
      spiralPoints.push(x + jitter, y + jitter, z);
    }
    spiralGeometry.setAttribute('position', new THREE.Float32BufferAttribute(spiralPoints, 3));
    spiralRef.current = new THREE.Points(spiralGeometry, spiralMaterialRef.current);
    scene.add(spiralRef.current);
    
    // Spiral 2
    const spiralGeometry2 = new THREE.BufferGeometry();
    spiralMaterial2Ref.current = new THREE.PointsMaterial({
      size: 0.05,
      transparent: true,
      opacity: 0.3 // Adjusted opacity
    });
    const spiralPoints2 = [];
    for (let i = 0; i < 15000; i++) {
      const t = i * 0.01;
      const x = a * Math.exp(b * t) * Math.cos(t + Math.PI);
      const y = a * Math.exp(b * t) * Math.sin(t + Math.PI);
      const z = 0; 
      const jitter = Math.random() * 0.015;
      spiralPoints2.push(x + jitter, y + jitter, z);
    }
    spiralGeometry2.setAttribute('position', new THREE.Float32BufferAttribute(spiralPoints2, 3));
    spiral2Ref.current = new THREE.Points(spiralGeometry2, spiralMaterial2Ref.current);
    scene.add(spiral2Ref.current);
    
    const animate = () => {
      animationFrameIdRef.current = requestAnimationFrame(animate);
      
      if (starFieldRef.current) starFieldRef.current.rotation.y += 0.0005;
      if (spiralRef.current) spiralRef.current.rotation.z += 0.001;
      if (spiral2Ref.current) spiral2Ref.current.rotation.z += 0.001;
      
      if (starsMaterialRef.current) {
        starsMaterialRef.current.color.set(currentThemeColors.foreground);
      }
      if (spiralMaterialRef.current) {
        spiralMaterialRef.current.color.set(currentThemeColors.primary);
      }
      if (spiralMaterial2Ref.current) {
        spiralMaterial2Ref.current.color.set(currentThemeColors.secondary);
      }
      
      if (rendererRef.current && sceneRef.current && cameraRef.current) {
        rendererRef.current.render(sceneRef.current, cameraRef.current);
      }
    };
    
    if (animationFrameIdRef.current) {
      cancelAnimationFrame(animationFrameIdRef.current);
    }
    animate();
    
    const handleResize = () => {
      const cam = cameraRef.current;
      const rend = rendererRef.current;
      const mount = mountRef.current;
      if (!mount || !cam || !rend) return;
      
      const newWidth = mount.clientWidth;
      const newHeight = mount.clientHeight;

      if (newWidth > 0 && newHeight > 0) {
        cam.aspect = newWidth / newHeight;
        cam.updateProjectionMatrix();
        rend.setSize(newWidth, newHeight);
      }
    };
    
    window.addEventListener('resize', handleResize);
    handleResize(); 

    return () => {
      if (animationFrameIdRef.current) {
        cancelAnimationFrame(animationFrameIdRef.current);
      }
      window.removeEventListener('resize', handleResize);
      
      starsGeometry?.dispose();
      spiralGeometry?.dispose();
      spiralGeometry2?.dispose();
      
      starsMaterialRef.current?.dispose();
      spiralMaterialRef.current?.dispose();
      spiralMaterial2Ref.current?.dispose();

      if (rendererRef.current) {
        rendererRef.current.dispose();
        if (rendererRef.current.domElement?.parentNode === currentMount) {
           currentMount.removeChild(rendererRef.current.domElement);
        }
      }
      
      sceneRef.current = null;
      cameraRef.current = null;
      rendererRef.current = null;
      starFieldRef.current = null;
      spiralRef.current = null;
      spiral2Ref.current = null;
      starsMaterialRef.current = null;
      spiralMaterialRef.current = null;
      spiralMaterial2Ref.current = null;
    };
  }, [currentThemeColors]); 

  return <div id="three-background-canvas" ref={mountRef} className="w-full h-full" />;
};

export default ThreeCanvas;
