
"use client";

import { useState, useRef, useEffect, type FormEvent } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetTrigger, SheetFooter } from '@/components/ui/sheet';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Bot, Send, UserCircle } from 'lucide-react'; // Brain icon import removed
import { askDvizioonAgent, type DvizioonAgentInput, type DvizioonAgentOutput } from '@/ai/flows/dvizioon-agent-flow';
import { useTranslations } from '@/hooks/useTranslations';
import Image from 'next/image'; // Added for logo

interface Message {
  id: string;
  sender: 'user' | 'agent';
  text: string;
  isLoading?: boolean;
}

interface ChatWidgetProps {
  initialMessage?: string;
  projectContext?: string;
  startOpen?: boolean;
}

export default function ChatWidget({ initialMessage, projectContext, startOpen = false }: ChatWidgetProps) {
  const t = useTranslations();
  const [isOpen, setIsOpen] = useState(startOpen);
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputValue, setInputValue] = useState('');
  const [isAgentTyping, setIsAgentTyping] = useState(false);
  const scrollAreaRef = useRef<HTMLDivElement>(null);
  const [currentProjectContext, setCurrentProjectContext] = useState<string | undefined>(projectContext);

  useEffect(() => {
    if (messages.length === 0) {
      setMessages([{ id: 'initial-widget', sender: 'agent', text: t.chatWidget.initialGreeting }]);
    }
  }, [t.chatWidget.initialGreeting, messages.length]);


  useEffect(() => {
    if (startOpen) {
      setIsOpen(true);
      if (initialMessage) {
        const userMessage: Message = {
          id: Date.now().toString() + '-initial-user',
          sender: 'user',
          text: initialMessage,
        };
        setMessages(prev => [...prev, userMessage]);
        handleSubmitLogic(initialMessage);
      }
    }
  }, [startOpen, initialMessage]);

  useEffect(() => {
    setCurrentProjectContext(projectContext);
  }, [projectContext]);

  const scrollToBottom = () => {
    if (scrollAreaRef.current) {
      const scrollViewport = scrollAreaRef.current.querySelector('div[data-radix-scroll-area-viewport]');
      if(scrollViewport) {
        scrollViewport.scrollTop = scrollViewport.scrollHeight;
      }
    }
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSubmitLogic = async (messageText: string) => {
    if (!messageText) return;
    setIsAgentTyping(true);

    const loadingMessageId = Date.now().toString() + '-loading';
    const loadingMessage: Message = {
      id: loadingMessageId,
      sender: 'agent',
      text: t.chatWidget.agentTyping,
      isLoading: true,
    };
    setMessages(prev => [...prev, loadingMessage]);
    scrollToBottom();

    try {
      const agentInput: DvizioonAgentInput = { userMessage: messageText };
      if (currentProjectContext) {
        agentInput.projectContext = currentProjectContext;
      }
      const response: DvizioonAgentOutput = await askDvizioonAgent(agentInput);
      const agentResponseMessage: Message = {
        id: Date.now().toString() + '-agent',
        sender: 'agent',
        text: response.agentResponse,
      };
      setMessages(prev => prev.filter(msg => msg.id !== loadingMessageId)); 
      setMessages(prev => [...prev, agentResponseMessage]);
      if (currentProjectContext) {
        setCurrentProjectContext(undefined); 
      }
    } catch (error) {
      console.error("Error calling Vizioon agent:", error);
      const errorMessage: Message = {
        id: Date.now().toString() + '-error',
        sender: 'agent',
        text: t.chatWidget.errorMessage,
      };
      setMessages(prev => prev.filter(msg => msg.id !== loadingMessageId));
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsAgentTyping(false);
      scrollToBottom();
    }
  };
  
  const handleFormSubmit = async (e?: FormEvent<HTMLFormElement>) => {
    if (e) e.preventDefault();
    const userMessageText = inputValue.trim();
    if (!userMessageText) return;

    const newUserMessage: Message = {
      id: Date.now().toString() + '-user',
      sender: 'user',
      text: userMessageText,
    };
    setMessages(prev => [...prev, newUserMessage]);
    setInputValue('');
    await handleSubmitLogic(userMessageText);
  };

  // N8N Modal placeholder removed as brain icon is removed

  return (
    <>
      <Sheet open={isOpen} onOpenChange={setIsOpen}>
        <SheetTrigger asChild>
          <Button
            variant="primary" // Keep primary or use a different variant if logo needs specific bg
            className="fixed bottom-6 right-6 h-16 w-16 rounded-full shadow-lg z-50 p-0 overflow-hidden group
                       bg-primary hover:bg-primary/90 
                       focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 
                       transform transition-all hover:scale-105 active:scale-95"
            aria-label={t.chatWidget.openChatLabel}
          >
            <Image 
              src="/dvizioon-logo.png" 
              alt={t.header.logoAlt} // Re-use logo alt from header for consistency
              width={48}  // Adjust size as needed, should be slightly smaller than button
              height={48}
              className="object-contain transition-transform duration-300 group-hover:scale-110" 
            />
          </Button>
        </SheetTrigger>
        <SheetContent side="right" className="w-[400px] sm:w-[540px] flex flex-col p-0">
          <SheetHeader className="p-4 border-b border-border flex flex-row justify-between items-center">
            <SheetTitle className="text-lg flex items-center gap-2">
              <Bot className="h-6 w-6 text-primary" />
              {t.chatWidget.title}
            </SheetTitle>
            {/* Brain button removed */}
          </SheetHeader>
          <ScrollArea className="flex-grow p-4" ref={scrollAreaRef}>
            <div className="space-y-4">
              {messages.map((msg) => (
                <div
                  key={msg.id}
                  className={`chat-message flex items-end gap-2 ${
                    msg.sender === 'user' ? 'user justify-end' : 'agent justify-start'
                  }`}
                >
                  {msg.sender === 'agent' && <Bot className="h-6 w-6 text-primary self-start shrink-0" />}
                  <div 
                    className={`p-3 rounded-lg max-w-[85%] break-words
                      ${msg.sender === 'user' ? 'bg-primary text-primary-foreground rounded-br-none' 
                                              : 'bg-card text-card-foreground rounded-bl-none border border-border/50'}
                      ${msg.isLoading ? 'animate-pulse' : ''}
                    `}
                  >
                    {msg.text.split('\n').map((line, i) => <p key={i}>{line}</p>)}
                  </div>
                  {msg.sender === 'user' && <UserCircle className="h-6 w-6 text-muted-foreground self-start shrink-0" />}
                </div>
              ))}
            </div>
          </ScrollArea>
          <SheetFooter className="p-4 border-t border-border bg-background">
            <form onSubmit={handleFormSubmit} className="flex w-full items-center space-x-2">
              <Input
                type="text"
                placeholder={t.chatWidget.inputPlaceholder}
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                className="flex-grow bg-input focus:bg-input/70"
                disabled={isAgentTyping}
                onKeyDown={(e) => {
                  if (e.key === 'Enter' && !e.shiftKey) {
                    e.preventDefault();
                    handleFormSubmit();
                  }
                }}
              />
              <Button type="submit" size="icon" className="bg-primary hover:bg-primary/90 text-primary-foreground" disabled={isAgentTyping || !inputValue.trim()}>
                <Send className="h-5 w-5" />
                <span className="sr-only">{t.chatWidget.sendButtonLabel}</span>
              </Button>
            </form>
          </SheetFooter>
        </SheetContent>
      </Sheet>
    </>
  );
}
